.searchbar{
    margin-top: 2%;
    padding-left: 30%;
    width:70%
}
.videoList{
    margin-top: 2%;
    padding-left: 20%;
    width:70%
}
.videoPlayer{
    padding-bottom: 56.25%;
    margin-left: 4%;
    height:0;
    position: relative;
}
.videoPlayer iframe{
    position: absolute;
    top:0;
    left:0;
    width:68% !important;
    height:65% !important;
}
.imgDisplayer{
    width:100% !important;
}
.cardContainer{
    display:flex !important;
    padding-left:10% !important;
    flex-direction:row !important;
    flex-wrap:wrap !important;
    justify-content: space-around !important
}
.individualCard{
    width:20%;
    margin:1%
}
.noShadow{
    box-shadow: none !important;
}
.imgSize{
    height:22vh !important;
}
@media screen and (max-width:768px){
    .searchbar{
        margin-top: 2%;  
        padding-left: 5%;      
        width:90%
    }
    .videoList{       
        width:100%;
        padding-left: 0%;
    }
    .videoPlayer{
        padding-bottom: 56.25%;        
        height:0;
        margin-left: 0%;
        position: relative;
    }
    .videoPlayer iframe{
        position: absolute;
        top:0;
        left:0;
        width:100% !important;
        height:100% !important;
    }
    .cardContainer{
        display:flex !important;       
        flex-direction:column !important;
        padding-left: 0% !important;
        flex-wrap:wrap !important;
        justify-content: space-around !important
    }
    .individualCard{
        width:auto;
        margin-top: 1%;        
    }
    .noShadow{
        box-shadow: initial !important;
    }
    .imgSize{
        height:32vh !important;
    }
}